import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { MyApp } from './app.component';
// import { HomePage } from '../pages/home/home';
// import { Menu } from '../pages/menu/menu';
// import {ProductsByCategory} from '../pages/products-by-category/products-by-category';
// import { ProductDetails } from '../pages/product-details/product-details';
import { Cart } from '../pages/cart/cart';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
// import { Signup } from '../pages/signup/signup';
// import { Login } from '../pages/login/login';
// import { Checkout } from '../pages/checkout/checkout';
import { HttpModule } from '@angular/http';
import { PayPal } from '@ionic-native/paypal';
// import { SearchPage } from '../pages/search/search';
import { IonicStorageModule } from '@ionic/storage';
import { OneSignal } from "@ionic-native/onesignal";
import { RestProvider } from '../providers/rest/rest';

@NgModule({
  declarations: [
    MyApp,
    Cart
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/checkout/checkout.module.ngfactory#CheckoutPageModuleNgFactory', name: 'Checkout', segment: 'checkout', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module.ngfactory#HomePageModuleNgFactory', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module.ngfactory#LoginModuleNgFactory', name: 'Login', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/menu/menu.module.ngfactory#MenuPageModuleNgFactory', name: 'Menu', segment: 'menu', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/product-details/product-details.module.ngfactory#ProductDetailsModuleNgFactory', name: 'ProductDetails', segment: 'product-details', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/products-by-category/products-by-category.module.ngfactory#ProductsByCategoryModuleNgFactory', name: 'ProductsByCategory', segment: 'products-by-category', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/search/search.module.ngfactory#SearchPageModuleNgFactory', name: 'SearchPage', segment: 'search', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/signup/signup.module.ngfactory#SignupModuleNgFactory', name: 'Signup', segment: 'signup', priority: 'low', defaultHistory: [] }
  ]
}),
    IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    Cart
  ],
  providers: [
    StatusBar,
    SplashScreen,
    PayPal,
    OneSignal,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    RestProvider
  ]
})
export class AppModule {}



